.sign__text {
  width: calc(90% - 60px);
  background-color: #F4BCD4;
  border-radius: 30px;
  padding: 0 0 0 10%;
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 1.1em;
  line-height: 56px;
  color: #ffffff;
  text-align: center;
}
@media (min-width: 520px) {
  .sign__text {
    font-size: 1.3em;
    line-height: 66px;
  }
}
@media (min-width: 768px) {
  .sign__text {
    font-size: 1.35em;
    padding: 0 0 0 8%;
  }
}
@media (min-width: 1440px) {
  .sign__text {
    font-size: 1.45em;
  }
}
.sign__icon {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 2px solid #F4BCD4;
  background-color: #ffffff;
}
@media (min-width: 520px) {
  .sign__icon {
    width: 64px;
    height: 64px;
    border: 3px solid #F4BCD4;
  }
}
.sign__icon-arrow {
  position: relative;
  display: block;
  width: 40%;
  height: 40%;
  border-bottom: 4px solid #598FFC;
  border-right: 4px solid #598FFC;
  transform: rotate(45deg);
  top: 30%;
  left: 29%;
  animation: pulse1 2s infinite;
}
.sign__icon-arrow::after {
  position: absolute;
  content: "";
  width: 4px;
  height: 160%;
  background-color: #598FFC;
  transform: rotate(-45deg);
  top: -30%;
  left: 40%;
  animation: pulse2 2s infinite;
}
.sign__icon-arrow:hover,
.sign__icon-arrow:hover::after {
  animation: none;
}
@keyframes pulse1 {
  0% {
    transform: rotate(45deg) scale(1);
  }
  50% {
    transform: rotate(45deg) scale(1.2);
  }
  100% {
    transform: rotate(45deg) scale(0.9);
  }
}
@keyframes pulse2 {
  0% {
    transform: rotate(-45deg) scale(1);
  }
  50% {
    transform: rotate(-45deg) scaleX(1.2);
  }
  100% {
    transform: rotate(-45deg) scale(0.9);
  }
}
